.container,
.main {
  width: 460px;
  min-height: 500px;
  color: #e1e1e1;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1f2024;
  font-family: Helvetica, sans-serif;
}
.container-center {
  justify-content: center;
}
.settings {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}
.streams-title {
  font-size: 14px;
  line-height: 1.1;
  color: #fff;
  margin-left: 10px;
}
.no-favorite-title {
  text-align: center;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 1.1;
  color: #fff;
  margin-left: 10px;
}
.all-streams {
  width: 100%;
  background-color: #1f2024;
}
.display-all-container {
  font-size: 14px;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 5px;
}
.label-display-all-checkbox {
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  background-color: #a970ff;
}
.checked-background {
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  background-color: #0f0f0f;
  color: #a970ff;
}
#display-all-checkbox {
  display: none;
}
