.stream {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 60px;
  background-color: #0f0f0f;
  align-items: flex-start;
  padding: 8px;
  margin: 8px;
  border-radius: 5px;
}
.stream-data {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.username {
  font-size: 18px;
  color: #fff;
}
.game-name {
  font-size: 16px;
  margin-top: 6px;
  margin-right: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.title {
  font-size: 12px;
  overflow: hidden;
  margin-right: 40px;
  margin-top: 6px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.viewer-count {
  font-size: 14px;
}
.picture-container {
  width: 105px;
  height: 59px;
  margin-right: 10px;
}
.picture {
  width: 105px;
  height: 59px;
}

.first-line {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.line-viewer {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.round {
  width: 9px;
  height: 9px;
  background-color: red;
  border-radius: 100%;
  margin-right: 5px;
}
.user-image {
  position: absolute;
  border-radius: 100%;
  bottom: 8px;
  right: 8px;
  width: 38px;
  height: 38px;
}
